<template>
  <div id="map" class="map-component">
    <div class="d-flex flex-row h-100">
      <div class="flex-fill d-flex flex-column">
        <div class="w-100 bg-primary text-light rounded p-2 select-mode" v-if="IsSelectAGVMode">選擇 AGV</div>
        <div class="w-100 text-light rounded p-2 select-mode"
          v-if="IsSelectEQStationMode"
          v-bind:class="TaskDispatchOptions.action_type == 'charge' ? 'bg-warning' : 'bg-primary'">{{ TaskDispatchOptions.action_type == 'charge' ? '選擇[充電站]' : `${TaskDispatchOptions.direction == 'source' ? '選擇[來源]設備' : '選擇[終點]設備'}` }}</div>
        <!-- 編輯選項 -->
        <div
          v-if="editable"
          class="editor-option bg-light"
          style="margin-top:24px">
          <div class="edit-block action-buttons">
            <b-button size="sm" variant="primary" @click="HandlerSaveBtnClick">儲存</b-button>
            <b-button size="sm" variant="danger" @click="ReloadMap">重新載入</b-button>
            <b-button size="sm" variant="danger" @click="ClearMap">重置圖資</b-button>
          </div>
          <div class="d-flex">
            <div class="edit-block">
              <span>
                <i class="bi bi-three-dots-vertical"></i>模式 </span>
              <el-radio-group
                v-bind:style="radio_group_style"
                v-model="EditorOption.EditMode"
                @change="(opt) => { RestoreOriginalPathStyle(this.selected_path_feature) }"
                size="large">
                <el-radio-button size="small" label="view">檢視[V]</el-radio-button>
                <el-radio-button size="small" label="edit">編輯[E]</el-radio-button>
              </el-radio-group>
            </div>
            <div class="edit-block d-flex">
              <span>
                <i class="bi bi-three-dots-vertical"></i>編輯動作 </span>
              <el-radio-group
                v-bind:style="radio_group_style"
                :disabled="EditorOption.EditMode != 'edit'"
                v-model="EditorOption.EditAction"
                @change="() => { ; RemoveInteraction(draw_forbid_regions_interaction); AddEditMapInteraction() }"
                size="large">
                <el-radio-button size="small" label="none">無</el-radio-button>
                <el-radio-button size="small" label="add-station">新增點位[1]</el-radio-button>
                <el-radio-button size="small" label="edit-station">編輯點位[2]</el-radio-button>
                <el-radio-button size="small" label="remove-station">移除點位[3]</el-radio-button>
              </el-radio-group>
              <div class="d-flex flex-column">
                <el-radio-group
                  class="mx-1"
                  :disabled="EditorOption.EditMode != 'edit'"
                  v-model="EditorOption.EditAction"
                  @change="() => { PathEditTempStore = []; RemoveInteraction(draw_forbid_regions_interaction); AddEditMapInteraction(); }"
                  size="large">
                  <el-radio-button size="small" label="add-path">新增路徑[4]</el-radio-button>
                  <el-radio-button size="small" label="edit-path">編輯路徑[5]</el-radio-button>
                  <el-radio-button size="small" label="remove-path">移除路徑[6]</el-radio-button>
                </el-radio-group>
                <el-radio-group
                  v-show="EditorOption.EditAction == 'add-path'"
                  class="mx-1 my-1"
                  v-model="EditorOption.AddPathMode.Direction"
                  @change="() => { RemoveInteraction(draw_forbid_regions_interaction); }"
                  size="large">
                  <el-radio-button size="small" label="one-direction">單向</el-radio-button>
                  <el-radio-button size="small" label="bi-direction">雙向</el-radio-button>
                </el-radio-group>
              </div>
              <div class="d-flex flex-column">
                <el-radio-group
                  class="mx-1"
                  :disabled="EditorOption.EditMode != 'edit'"
                  v-model="EditorOption.EditAction"
                  size="large">
                  <el-radio-button @click="HandleAddForbidRegionClicked(EditorOption.AddRegionMode.Mode)" size="small" label="add-forbid-region">新增管制區[7]</el-radio-button>
                  <el-radio-button @click="HandleEditForbidRegionClicked" size="small" label="edit-forbid-region">編輯管制區[8]</el-radio-button>
                  <el-radio-button @click="HandleDeleteForbidRegionClicked" size="small" label="remove-forbid-region">移除管制區[9]</el-radio-button>
                </el-radio-group>
                <el-radio-group
                  v-show="EditorOption.EditAction == 'add-forbid-region'"
                  class="mx-1 my-1"
                  v-model="EditorOption.AddRegionMode.Mode"
                  size="large">
                  <el-radio-button @click="HandleAddForbidRegionClicked('forbid')" size="small" label="forbid">禁制區</el-radio-button>
                  <el-radio-button @click="HandleAddForbidRegionClicked('passible')" size="small" label="passible">通行區</el-radio-button>
                </el-radio-group>
              </div>
            </div>
          </div>
          <!-- <div
            v-show="ShowWarningNotify"
          class="bg-warning text-light border rounded p-1">目前為Slam座標模式，點位位置即為AGV真實走行座標，請小心操作 </div>-->
        </div>
        <div class="d-flex flex-row" style="overflow-y: hidden;">
          <!-- settings tabcontrol -->
          <div
            v-if="EditorOption.EditMode == 'edit' && editable"
            v-bind:class="left_tab_class_name"
            class="border bg-light">
            <div class="p-0 m-0 w-100" v-if="left_tab_class_name == 'tab-close'">
              <i
                @click="() => {
                  left_tab_class_name = left_tab_class_name == 'tab-open' ? 'tab-close' : 'tab-open'
                }"
                class="bi bi-chevron-double-right"></i>
            </div>
            <div v-else class="tab-open">
              <i
                @click="() => {
                  left_tab_class_name = left_tab_class_name == 'tab-open' ? 'tab-close' : 'tab-open'
                }"
                class="bi bi-chevron-double-left"></i>
              <b-tabs class="p-1">
                <b-tab title="點位">
                  <div class="border">123</div>
                </b-tab>
                <b-tab title="路徑" active>
                  <div class="border">
                    <!-- <div class="d-flex">
                    <div style="width:70px">搜尋</div> <el-input></el-input>
                    </div>-->
                    <el-table
                      :data="PathesSegmentsForEdit"
                      highlight-current-row
                      row-key="StartPtIndex"
                      @row-click="HandlePathTbRowClick"
                      border
                      style="height: 650px;"
                      size="small">
                      <el-table-column label="起點" prop="StartPtIndex" width="120">
                        <template #default="scope">
                          <b>{{ GetPointName(scope.row.StartPtIndex) }}</b>
                        </template>
                      </el-table-column>
                      <el-table-column label="終點" prop="EndPtIndex" width="120">
                        <template #default="scope">
                          <b>{{ GetPointName(scope.row.EndPtIndex) }}</b>
                        </template>
                      </el-table-column>
                      <el-table-column label="操作">
                        <template #default="scope">
                          <div class="d-flex flex-row">
                            <b-button size="sm" variant="primary">編輯</b-button>
                            <b-button
                              class="mx-1"
                              size="sm"
                              variant="danger"
                              @click="HandlePathRemoveBtnClick(scope.row)">移除</b-button>
                          </div>
                        </template>
                      </el-table-column>
                      <!-- <el-table-column label="是否為工位路線" prop="IsEQLink"></el-table-column> -->
                    </el-table>
                  </div>
                </b-tab>
              </b-tabs>
            </div>
          </div>
          <div class="w-100 d-flex flex-row">
            <!-- Map Render -->
            <div
              :id="id"
              v-bind:style="{ height: canva_height }"
              class="agv_map flex-fll"
              @contextmenu="showContextMenu($event)">
            </div>
            <!--提示-->
            <div class="notifiers" style="position:absolute;width:100%">
              <el-alert v-if="map_name == 'Unkown'" title="載入中" type="warning" effect="dark" />
              <el-alert class="notify-text" v-if="editable && EditorOption.EditAction == 'add-station'" title="使用滑鼠[右鍵]點擊地圖新增點位" type="success" />
              <el-alert class="notify-text" v-if="editable && EditorOption.EditAction == 'edit-station'" title="使用滑鼠[右鍵]選擇欲編輯之點位" type="success" />
              <el-alert class="notify-text" v-if="editable && EditorOption.EditAction == 'remove-station'" title="使用滑鼠[左鍵]選擇欲刪除之點位" type="error" />
              <el-alert class="notify-text" v-if="editable && EditorOption.EditAction == 'add-path' && EditorOption.AddPathMode.Direction == 'one-direction'" title="使用滑鼠[左鍵]點擊地圖上任意兩個點位以新增一條單行道" type="success" />
              <el-alert class="notify-text" v-if="editable && EditorOption.EditAction == 'add-path' && EditorOption.AddPathMode.Direction == 'bi-direction'" title="使用滑鼠[左鍵]點擊地圖上任意兩個點位以新增一條雙向道" type="success" />
              <el-alert class="notify-text" v-if="editable && EditorOption.EditAction == 'edit-path'" title="使用滑鼠[右鍵]選擇欲編輯之路徑" type="success" />
              <el-alert class="notify-text" v-if="editable && EditorOption.EditAction == 'remove-path'" title="使用滑鼠[左鍵]選擇欲刪除之路徑" type="error" />
              <el-alert class="notify-text" v-if="editable && EditorOption.EditAction == 'add-forbid-region'" :title="`使用滑鼠[左鍵/右鍵]在地圖上繪製[${EditorOption.AddRegionMode.Mode == 'forbid' ? '禁制' : '通行'}]區(按下[ESC]可取消繪製)`" type="success" />
              <el-alert class="notify-text" v-if="editable && EditorOption.EditAction == 'edit-forbid-region'" title="使用滑鼠[左鍵]在地圖上選取欲編輯之管制區)" type="success" />
              <el-alert class="notify-text" v-if="editable && EditorOption.EditAction == 'remove-forbid-region'" title="使用滑鼠[左鍵]在地圖上點擊欲刪除之管制區" type="error" />
            </div>
            <div v-if="false" class="ol-control custom-buttons">
              <button @click="HandleSettingBtnClick">
                <i class="bi bi-sliders"></i>
              </button>
              <button @click="HandleSettingBtnClick">?</button>
            </div>
            <div v-if="true" class="cursour-coordination-show d-flex flex-column">
              <span style="color:rgb(24, 24, 24)">{{ MouseCoordinationDisplay }}</span>
              <div class="grid-size-text">Grid Size:{{ MapGridSize }}m</div>
            </div>
            <!-- 設定 -->
            <div
              class="options bg-light border-start text-start px-1 py-3">
              <div v-if="station_show" class="rounded d-flex flex-column">
                <span class="border-bottom">顯示名稱</span>
                <el-radio-group
                  v-model="station_name_display_mode"
                  @change="StationNameDisplayOptHandler">
                  <el-radio label="index" size="large">Index</el-radio>
                  <el-radio label="name" size="large">Name</el-radio>
                  <el-radio label="tag" size="large">Tag</el-radio>
                </el-radio-group>
              </div>
              <div>
                <span class="mx-1">地圖模式</span>
                <el-switch
                  @change="MapDisplayModeOptHandler"
                  inactive-value="router"
                  active-value="coordination"
                  width="70"
                  v-model="map_display_mode"
                  inline-prompt
                  inactive-text="路網"
                  active-text="Slam"
                  inactive-color="seagreen"></el-switch>
              </div>
              <div v-if="agv_show">
                <span class="mx-1">AGV 顯示</span>
                <el-switch
                  @change="AgvDisplayOptHandler"
                  inactive-value="none"
                  active-value="visible"
                  width="70"
                  v-model="agv_display"
                  inline-prompt
                  inactive-text="隱藏"
                  active-text="顯示"
                  inactive-color="rgb(146, 148, 153)"></el-switch>
              </div>
              <div>
                <span class="mx-1">地圖背景</span>
                <el-switch
                  v-model="map_image_display"
                  inactive-value="none"
                  inactive-text="隱藏"
                  active-value="visible"
                  active-text="顯示"
                  inactive-color="rgb(146, 148, 153)"
                  inline-prompt
                  width="70"
                  @change="SlamImageDisplayOptHandler"></el-switch>
              </div>
              <div>
                <span class="mx-1">路網顯示</span>
                <el-switch
                  v-model="routePathsVisible"
                  inactive-text="隱藏"
                  active-text="顯示"
                  inline-prompt
                  inactive-color="rgb(146, 148, 153)"
                  width="70"
                  @change="(visible) => {
                    if (visible) {
                      if (map_display_mode == 'router') {
                        PathLayerForCoordination.setVisible(false);
                        PathLayerForRouter.setVisible(true);
                      } else {
                        PathLayerForCoordination.setVisible(true);
                        PathLayerForRouter.setVisible(false);
                      }
                    } else {
                      PathLayerForCoordination.setVisible(false);
                      PathLayerForRouter.setVisible(false);
                    }
                    HideNormalStations(!visible);
                  }"></el-switch>
              </div>
              <div>
                <span class="mx-1">管制區顯示</span>
                <el-switch
                  :disabled="map_display_mode != 'coordination'"
                  v-model="regionsVisible"
                  inactive-text="隱藏"
                  active-text="顯示"
                  inline-prompt
                  inactive-color="rgb(146, 148, 153)"
                  width="70"
                  @change="(visible) => {
                    RegionLayer.setVisible(visible && map_display_mode == 'coordination');
                  }"></el-switch>
              </div>
              <div v-if="editable" class="rounded">
                <el-tooltip content="開啟後於車載畫面上傳座標資訊後將會自動新增點位至地圖上">
                  <span class="mx-1">AGV上報點位模式</span>
                </el-tooltip>
                <el-switch
                  class="my-2"
                  inactive-text="OFF"
                  active-text="ON"
                  inline-prompt
                  width="70"
                  v-model="agv_upload_coordination_mode"
                  @change="HandleAGVUploadCorrdinationChanged"></el-switch>
              </div>
            </div>
          </div>
        </div>
        <MapSettingsDialog ref="settings"></MapSettingsDialog>
      </div>
    </div>
    <PointContextMenu
      ref="EditModeContextMenu"
      v-show="editModeContextMenuVisible"
      :mouse_click_position="[contextMenuTop, contextMenuLeft]"
      :options="contextMenuOptions"
      @OnTaskBtnClick="HandleMenuTaskBtnClick"
      @OnPtSettingBtnClick="HandlePtSettingBtnClick"></PointContextMenu>
    <MapPointSettingDrawer
      ref="ptsetting"
      @OnLeve="HandlePtSettingDrawerLeaved"
      @OnPointSettingChanged="PointSettingChangedHandle"></MapPointSettingDrawer>
    <MapPathSettingDrawer :SettingsChangedHandler="() => {
      UpdateStationPathLayer();
      HandlePathTbRowClick(SelectedPathData);

    }" @closed="HandlePathSetingDrawerClosed" ref="path_editor"></MapPathSettingDrawer>
    <MapRegionEditDrawer @closed="HandleForbidRegionEditDrawerClosed" ref="forbid_region_editor"
      :SettingsChangedHandler="() => {

      }"></MapRegionEditDrawer>
    <el-dialog @closed="() => {
      if (selected_path_feature) {
        RestoreOriginalPathStyle(selected_path_feature)
      }
    }" draggable width="600" title="路徑選取" v-model="ShowPathSelectDialog">
      <div class="bg-light text-start d-flex py-2">
        <b-button size="sm" variant="success" @click="() => {
          PathesCandicats.forEach(path_setting => {
            path_setting.IsPassable = true
          });
          UpdateStationPathLayer();
          HandlePathTbRowClick(SelectedPathData);
        }">開放所有道路</b-button>
        <b-button class="mx-2" variant="danger" size="sm" @click="() => {
          PathesCandicats.forEach(path_setting => {
            path_setting.IsPassable = false
          });
          UpdateStationPathLayer();
          HandlePathTbRowClick(SelectedPathData);
        }">關閉所有道路</b-button>
      </div>
      <el-table :data="PathesCandicats" border>
        <el-table-column label="ID" prop="PathID">
          <template #default="scope">
            <el-tag effect="dark">{{ scope.row.PathID }}</el-tag>
          </template>
        </el-table-column>
        <el-table-column label="起點Index" prop="StartPtIndex"></el-table-column>
        <el-table-column label="終點Index" prop="EndPtIndex"></el-table-column>
        <el-table-column label="狀態" prop="IsPassable">
          <template #default="scope">
            <el-tag effect="dark" :type="scope.row.IsPassable ? 'success' : 'danger'">{{ scope.row.IsPassable ? '開放' : '封閉' }}</el-tag>
          </template>
        </el-table-column>
        <el-table-column>
          <template #default="scope">
            <b-button variant="primary" @click="HandlePathSelected(scope.row)">選取</b-button>
          </template>
        </el-table-column>
      </el-table>
    </el-dialog>
  </div>
</template>

<script>

import Feature from 'ol/Feature.js';
import Map from 'ol/Map.js';
import Point from 'ol/geom/Point.js';
import VectorSource from 'ol/source/Vector.js';
import LineString from 'ol/geom/LineString';
import { Pointer } from 'ol/interaction'
import Draw from 'ol/interaction/Draw.js';
import Projection from 'ol/proj/Projection.js';
import Static from 'ol/source/ImageStatic.js';
import { ZoomSlider } from 'ol/control.js';
import View from 'ol/View.js';
import ImageLayer from 'ol/layer/Image.js';
import { Vector as VectorLayer } from 'ol/layer.js';
import { Fill, Stroke, Style, Circle as CircleStyle, Text } from 'ol/style';
import { Circle, Polygon } from 'ol/geom';

import { noModifierKeys } from 'ol/events/condition';
import { watch } from 'vue'
import bus from '@/event-bus.js'
import { clsMapStation, MapPointModel, clsAGVDisplay, MapRegion } from './mapjs';
import { GetStationStyle, CreateStationPathStyles, CreateEQLDULDFeature, CreateLocusPathStyles, AGVPointStyle, AGVCargoIconStyle, MapContextMenuOptions, MenuUseTaskOption, ChangeCargoIcon, createBezierCurvePoints, CreateNewStationPointFeature, CreateStationFeature, GetPointByIndex, CreateLocIcon, CreateTransTaskMark, CreateRegionPolygon } from './mapjs';
import { MapStore } from './store'
import { EqStore, agv_states_store } from '@/store'
import MapSettingsDialog from './MapSettingsDialog.vue';
import PointContextMenu from './MapContextMenu.vue';
import MapPointSettingDrawer from '../MapPointSettingDrawer.vue';
import MapPathSettingDrawer from './MapPathSettingDrawer.vue'
import MapRegionEditDrawer from './MapRegionEditDrawer.vue';
import QuicklyAction from './QuicklyActionMenu.vue'
import { ElNotification } from 'element-plus'
import { Throttle } from '@/api/Common/UtilityTools.js'
export default {
  components: {
    QuicklyAction, MapSettingsDialog, PointContextMenu, MapPointSettingDrawer, MapPathSettingDrawer, MapRegionEditDrawer,

  },
  props: {
    editable: {
      type: Boolean,
      default: false
    },
    agv_show: {
      type: Boolean,
      default: true
    },
    station_show: {
      type: Boolean,
      default: true
    },
    eq_lduld_status_show: {
      type: Boolean,
      default: false
    },
    agv_upload_coordi_data: {
      type: Object,
      default() {
        return {}
      }
    },
    task_dispatch_menu_show: {
      type: Boolean,
      default: false
    },
    id: {
      type: String,
      default() {
        return 'map'
      }
    },
    canva_height: {
      type: String,
      default: '670px'
    }
  },
  data() {
    return {
      map: new Map(),
      map_name: 'Unkown',
      zoom: 2,
      zoom_route: 2,
      center: [0, 0],
      center_route: [0, 0],
      loading: false,
      ShowPathSelectDialog: false,
      IsPathEditing: false,
      SelectedPathData: {},
      PathesCandicats: [],
      _map_stations: [],
      ImageLayer: new ImageLayer(),
      /**Slam座標圖層 */
      PointLayer: new VectorLayer({
        source: new VectorSource({
          features: [],
        }),
        zIndex: 2
      }),
      /**路網圖層 */
      PointRouteLayer: new VectorLayer({
        source: new VectorSource({
          features: [],
        }),
        zIndex: 2
      }),
      /**路網圖層 */
      EQLDULDStatusLayer: new VectorLayer({
        source: new VectorSource({
          features: [],
        }),
        zIndex: 2
      }),
      PathLayerForCoordination: new VectorLayer({
        source: new VectorSource({
          features: [],
        }),
        zIndex: 0
      }),
      PathLayerForRouter: new VectorLayer({
        source: new VectorSource({
          features: [],
        }),
        zIndex: 0
      }),
      //路網(路線)
      AGVLocLayer: new VectorLayer({
        source: new VectorSource({
          features: [],
        }),
        zIndex: 4
      }),
      AGVLocusLayer: new VectorLayer({
        source: new VectorSource({
          features: [],
        }),
        zIndex: 3
      }), //軌跡圖顯示圖層
      CustomLayer: new VectorLayer({
        source: new VectorSource(
          {
            features: []
          }
        )
      }),
      TransferTaskIconLayer: new VectorLayer({
        source: new VectorSource(
          {
            features: []
          }
        ),
        zIndex: 22
      }),
      RegionLayer: new VectorLayer({
        source: new VectorSource(
          {
            features: []
          }
        ),
        zIndex: 22
      }),
      AGVFeatures: {},
      MouseCoordination: undefined,
      FeatureKeys: {
        Station: 'station',
        path: 'path',
        nav_path: 'nav_path',
        agv: 'agv'
      },
      IsDragging: false,
      IsAddPathMode: true,
      PathEditTempStore: [],
      EditorOption: {
        // EditMode: 'view',
        EditMode: 'edit',
        EditAction: 'none',
        AddPathMode: {
          Direction: 'one-direction'
        },
        AddRegionMode: {
          Mode: 'forbid' //forbid;passible
        }
      },
      /**顯示模式 : coordination 實際座標 ; router 整齊的路網*/
      map_display_mode: 'router',
      station_name_display_mode: 'name',
      agv_display: 'visible',
      map_image_display: 'visible',
      left_tab_class_name: 'tab-close',
      previousSelectedFeatures: [],
      agv_upload_coordination_mode: false,
      editModeContextMenuVisible: false,
      taskDispatchContextMenuVisible: false,
      routePathsVisible: true,
      regionsVisible: true,
      contextMenuTop: 0,
      contextMenuLeft: 0,
      contextMenuOptions: new MapContextMenuOptions(),
      firstUseFlag: true,
      selected_path_feature: undefined,
      path_highlight_style: new Style({
        stroke: new Stroke({
          color: 'blue',
          width: 8
        })
      }),
      PathesSegmentsForEdit: [],
      IsSelectAGVMode: false,
      IsSelectEQStationMode: false,
      TaskDispatchOptions: {
        action_type: '',
        direction: '',
        stations_to_show: []
      },
      draw_forbid_regions_interaction: undefined,
      delete_forbid_regions_interaction: undefined,
      edit_forbid_regions_interaction: undefined,
      mapEditsInteraction: undefined
    }
  },
  computed: {
    MapServerUrl() {
      return MapStore.getters.MapServerUrl;
    },
    map_img_extent() {
      return MapStore.getters.Map_Image_Boundary;
    },
    map_img_size() {
      return MapStore.getters.Map_Image_Size;
    },
    PathesSegments() {
      return MapStore.getters.Pathes;
    },
    previousSelectedFeature() {
      return this.previousSelectedFeatures[0]
    },
    MouseCoordinationDisplay() {
      if (!this.MouseCoordination) {
        return '(-1,-1)'
      }
      return `(${this.MouseCoordination[0].toFixed(2)},${this.MouseCoordination[1].toFixed(2)})`
    },
